@import url(https://fonts.googleapis.com/css?family=Poppins:300,400,600,800&display=swap);

html,
body {
  padding: 0;
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; }

.container {
  width: 1280px;
  min-width: 1280px;
  margin: 0 auto; }
  .container .wrapper {
    padding: 0 48px; }
    .container .wrapper .home {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      color: white; }
      .container .wrapper .home p {
        font-size: 0.75rem;
        letter-spacing: 0.3rem;
        text-transform: uppercase;
        font-weight: 500; }
      .container .wrapper .home h5 {
        margin-top: 260px;
        font-size: 1.5rem;
        font-weight: 600;
        padding-right: 360px; }

header {
  height: 100px; }
  header .inner-header {
    position: relative;
    z-index: 10;
    height: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    header .inner-header .logo a {
      font-weight: 700;
      text-decoration: none;
      color: white; }
    header .inner-header .menu button {
      border: none;
      background: none;
      outline: none;
      cursor: pointer;
      font-size: 0.8rem;
      color: white; }
  header .hamburger-menu {
    display: none;
    z-index: 9;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
    height: 100%;
    width: 100%; }
    header .hamburger-menu .menu-secondary-background-color {
      background-color: white;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      position: fixed;
      height: 100%;
      width: 100%;
      z-index: -1; }
    header .hamburger-menu .menu-layer {
      position: relative;
      background: darksalmon;
      height: 100%;
      overflow: hidden; }
      header .hamburger-menu .menu-layer .menu-city-background {
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        position: absolute;
        height: 100%;
        width: 100%;
        opacity: 0;
        background-size: cover;
        background-repeat: no-repeat;
        -webkit-animation: cameraPan 30s infinite;
                animation: cameraPan 30s infinite; }
      header .hamburger-menu .menu-layer .wrapper {
        position: relative; }
        header .hamburger-menu .menu-layer .wrapper .menu-links {
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;
          top: 200px; }
          header .hamburger-menu .menu-layer .wrapper .menu-links nav {
            display: block; }
            header .hamburger-menu .menu-layer .wrapper .menu-links nav ul {
              padding: 0;
              margin: 0; }
              header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li {
                list-style: none;
                font-size: 6rem;
                font-weight: 700;
                cursor: pointer;
                height: 135px;
                overflow: hidden;
                position: relative;
                width: 700px; }
                header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a {
                  position: absolute;
                  color: white;
                  text-decoration: none; }
                  header .hamburger-menu .menu-layer .wrapper .menu-links nav ul li a:hover {
                    color: black; }
          header .hamburger-menu .menu-layer .wrapper .menu-links .info {
            color: white;
            width: 300px; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info h3 {
              font-size: 1.2rem;
              margin: 8px auto; }
            header .hamburger-menu .menu-layer .wrapper .menu-links .info p {
              margin: 0 auto;
              font-size: 0.8rem; }
        header .hamburger-menu .menu-layer .wrapper .locations {
          position: absolute;
          bottom: -80px;
          color: white;
          margin-top: 16px;
          font-size: 0.8rem;
          font-weight: 600; }
          header .hamburger-menu .menu-layer .wrapper .locations span {
            font-weight: 400;
            cursor: pointer;
            margin: 0 32px;
            transition: 0.3s ease-in-out; }
            header .hamburger-menu .menu-layer .wrapper .locations span:first-child {
              margin-left: 64px; }
            header .hamburger-menu .menu-layer .wrapper .locations span:hover {
              background: black;
              padding: 8px 24px;
              border-radius: 4px; }

.homebg {
  background-image: url(/styl/static/media/homebackground.d3fc164d.jpg);
  position: absolute;
  height: 100%;
  width: 100%;
  opacity: 1;
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1; }
  .homebg .layer {
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }

@-webkit-keyframes cameraPan {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0% 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

@keyframes cameraPan {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0% 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

